/* pwa install */
.hidden {
  display: none !important;
}

#installContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  background: #f47a0b;
  padding: 1rem;
  margin: 0 auto;
}

#installContainer .label{
  font-weight: 500;
    font-size: .8rem;
    color: #fff;
    padding: 0 1rem;
}

#installContainer button {
  background-color: #fff;
    border: 1px solid white;
    color: inherit;
    font-size: 1em;
    padding: 0.5em;
    border-radius: 1rem;
    width: 8rem;
}

.App-loading {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-loading-simple {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}



.App-link {
  color: #09d3ac;
}

.App-container {
  display: flex;
  flex-direction: column;
}

main {
  /* height: 100vh; */
} 


.App-dashboard {
  display: grid;
  grid-template-columns: 1fr 33.33%;
  grid-gap: 20px;
}

.App-dashboard__main-view {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.App-leaderboard {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.161);
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  background: #FFF;
}

.App-dashboard__stats {
  display: flex;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px;
  border: none;
}

.App-dashboard__stats>div {
  /* height: 100px;  */
  flex: 0 0 16.6666666667%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.App-dashboard__stats>div:first-child,
.App-dashboard__stats>div:last-child {
  flex: 0 1 25%;
  justify-content: left;
  padding: 20px;
}

